ta;  Personal Data means any information relating to an identified or identifiable natural person (USER);  Processing means any operation or set of operations which is performed on personal data;  Recipient means a natural or legal person, public authority, agency or another body, to which the personal data are disclosed, whether a third party or not;  Personal data breach means a breach of security leading to the accidental or unlawful destruction, loss, alteration, unauthorized disclosure of, or access to, personal data transmitted, stored or otherwise processed. ")]),_c('p',[_vm._v(" III. Personal data collection The personal data Superhero.com collects is minimized completely, selecting only those types of information, which are adequate, relevant and limited to what is necessary for the use of the Website. Superhero.com collects two main groups of personal data according to their purpose: 3.1. Personal data in relation to indirect identifiers The donations and comments are conducted via the Extension Superherowallet. The Superhero wallet is a blockchain based application on which an account address is generated for user identification. Once the donation and comment are processed, they appear on the Website with a hyperlink forwarding to the website the User has tipped. Furthermore, other Users on the Website can additionally donate and comment on news added to the Website. Therefore, the account address will appear on the Website. The account addresses can be treated as indirect identifiers. An indirect identifier is a value which cannot be used alone to identify a data subject, however, could be identifying if combined with other indirect identifiers. The processing is necessary for the purpose of provision of the Service- namely the Website. The retention period for the personal data is until termination of the Service provision. 3.2. Personal data in relation to User support and communication During the use of our Website, users may decide to contact the team in relation to website support, troubleshoot problems, questions, etc. The collection of the personal data is initiated by Users and they decide what type of personal data to include in the communication. Therefore, the legal basis for processing is that the User has given consent to the processing of their personal data for one or more specific purposes. The retention period is until the questions have been answered, assistance from the team has been provided, the problems have been solved or in any other case, until withdrawal of consent. Superhero.com may retent personal information for additional period of time, if it is required by law or for crime/breach/ infringement prevention and safety. ")]),_c('p',[_vm._v(" IV. Your rights: YOU have the right to:  Request from Superhero.com access to the personal data that we hold about You in a portable format.  Request from Superhero.com correction of any collected personal data. The information may include only your personal data collection.  Receive a copy of your personal data in electronic format.  The right to be forgotten- You have the right to request the deletion of your personal data at any time where the retention of such data infringes relevant legislation.  Receive information from Us about Our activities in connection to your personal data, including the purposes of collection and storage, the period of time for storage, the methods of collecting, the presence of automated processing.  Receive your data and transfer them to another controller.  You have the right to lodge a complaint with a supervisory authority, in particular in the Member State of your habitual residence, place of work or place of the alleged infringement if You consider that there is personal data breach. ")]),_c('p',[_vm._v(" V. Sharing of Personal Information with Third Parties- Recipients  We may disclose USERS identifiable personal information to third parties under special circumstances: (i) to comply with a legal requirement, judicial proceeding, court order, or legal process served on Superhero.com or its affiliates, (ii) to investigate a possible crime, such as fraud or identity theft; (iii) to Public Authorities in accordance with their legal obligation for the exercise of their official mission, such as tax and customs authorities, financial investigation units, independent administrative authorities, or financial marker authorities responsible for supervision of securities markets; (iv) in connection with the sale, purchase, business transition under any form, merger, dissolution, reorganization, liquidation of Superhero.com; We may have to disclose the information You have given us to the successor, who is part of the transition, (v) when We believe it is necessary to protect the rights, property, or safety of Superhero.com or other persons, or (vi) as otherwise required or permitted by law, including any contractual obligations of Superhero.com. ")]),_c('p',[_vm._v(" VI. Children and Privacy  Superhero.com does not process information of persons under the age of obtaining full legal capacity (in most countries 18 years old) due to the character of the Website and the Wallet.  Superhero.com will immediately delete any personal data referring to persons under the age of full legal capacity.  We are not liable in any way if YOU provide false information about your age or use the Website before YOU have turned the above-mentioned age. ")]),_c('p',[_vm._v(" VII. Breach measures In case of breach, We will undertake every possible action according to the applicable legislation in an appropriate and timely manner, to avoid any material or non-material damage to Users and to protect the personal data of Users. ")]),_c('p',[_vm._v(" VIII. How Do I contact Superhero.com ? To contact us with your questions or comments regarding this Policy or the information collection and dissemination practices of Superhero.com relevant to your use of the Website, please email us at superherowallet@protonmail.com. ")])])])])}]

